.App {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

main {
    flex: 1
}

section {
    position: relative;
    padding: 80px 0;

    &.about-us,
    &.services,
    &.contact-us {
        background-color: $dark-grey;
    }

    @media only screen and (max-width: $mobile) {
        padding: 60px 0;
    }
}

.anchor-link {
    position: absolute;
    top: - $header-height-minimised;

    @media only screen and (max-width: 1000px) {
        top: -$mobile-header-height;
    }
}

.container {
    max-width: 1800px;
    padding: 0 40px;
    margin: 0 auto;

    @media only screen and (max-width: 1920px) {
        max-width: 1100px;
    }

    @media only screen and (max-width: 1000px) {
        padding: 0 20px;
    }
}

.contact-wrapper {
    display: flex;
    justify-content: center;
}