.motors {
    margin-top: $header-height;

    @media only screen and (max-width: $mobile) {
        margin-top: $mobile-header-height;
    }

    h1 {
        color: black;
        padding: 20px 0 40px;
    }

    .motors-grid {
        display: flex;
        column-gap: 30px;

        @media only screen and (max-width: $mobile) {
            flex-wrap: wrap;
        }

        .motor {
            width: 33.33%;
            margin-bottom: 50px;

            @media only screen and (max-width: $tablet) {
                width: 50%;
            }

            @media only screen and (max-width: $mobile) {
                width: 100%;
            }

            .img-container {
                position: relative;
                width: 100%;
                height: 300px;

                @media only screen and (max-width: $desktop) {
                    height: 220px;
                }

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    transition: scale 0.4s ease-in-out;
                    border-radius: 5px;
                }

                &::before {
                    position: absolute;
                    content: '';
                    left: -8px;
                    top: -8px;
                    width: 100%;
                    height: 100%;
                    border: 2px solid $dark-purple;
                    transition: all 0.4s ease-in-out;
                    z-index: -1;
                    border-radius: 5px;
                }
            }

            p {
                color: black;
            }

            &:hover {
                .img-container {
                    img {
                        scale: 1.04;
                    }

                    &::before {
                        top: 0;
                        left: 0;
                    }
                }
            }
        }
    }
}